.adventage {
  &--color {
    color: #ffffff;
    background-color: $blue;
  }

  &--grey {
    background-color: $gray-bg;
  }
}

.adventage__inner-list {
  display: block;

  @media (min-width: $tablet-width) {
    display: flex;
  }

  // @media (min-width: $desktop-width) {
  // }
}

.adventage__wrapper {
  @include index-padding;
  @include flex-centring-column;
  padding-top: 24px;
  padding-bottom: 24px;
  position: relative;

  @media (min-width: $tablet-width) {
    width: $adventage-tablet;
    height: $adventage-tablet;
    padding: 33px 33px;
  }

  @media (min-width: $desktop-width) {
    width: $adventage-desktop;
    padding: 50px 50px;
    height: $adventage-tablet;
  }

  &--color {
    padding-top: 37px;
    padding-bottom: 37px;
  }
}

.adventages__inner-list .adventages__item:not(:last-of-type)::after {
  @media (max-width: $mobile-width-only) {
    content: "";
    position: absolute;
    border-bottom: 1px solid rgba(214, 214, 214, 1);
    width: 284px;
    height: 1px;
    bottom: 0;
    left: 18px;
  }
}

.adventage__with-image-wrappper {
  @include list-reset;
  list-style-type: none;
  display: flex;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-direction: row;
    // height: 256px;
  }

  // @media (min-width: $desktop-width) {
  // }
}

.adventage__title {
  margin-bottom: $adventage-title-margin;
}

.adventage__description {
  margin: 0;
  text-align: center;
}

.adventage__number {
  margin-bottom: 20px;
}

.adventage__image {
  display: block;
  width: 100%;
  height: auto;
}
